"use client";
import React, { useState, useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import { updateFilter, resetFilter } from "../_lib/genericReducer";
import { getCategory } from "../_service/ProductService";
import { FaTimes } from "react-icons/fa";
import { Button } from "@/components/ui/button";
import { Dialog, DialogContent, DialogHeader, DialogTitle, DialogTrigger } from "@/components/ui/dialog";
import { Input } from "@/components/ui/input";
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "@/components/ui/select";
import { Checkbox } from "@/components/ui/checkbox";
import { RadioGroup, RadioGroupItem } from "@/components/ui/radio-group";
import { Label } from "@/components/ui/label";

function Filter() {
  const filter = useSelector((state) => state.generic);
  const dispatch = useDispatch();
  const [data, setData] = useState([]);
  
  useEffect(() => {
    async function loadCategories() {
      const fetchedData = await getCategory();
      setData(fetchedData.data);
    }
    loadCategories();
  }, []);
  
  const [filters, setFilters] = useState({
    selectedCategory: "",
    selectedSubcategories: [],
    sortOption: "",
    minPrice: "",
    maxPrice: "",
  });

  const [isModalOpen, setIsModalOpen] = useState(false);  

  const handleCategoryChange = (category) => {
    setFilters((prevFilters) => ({
      ...prevFilters,
      selectedCategory: category,
      selectedSubcategories: [], 
    }));
  };

  const handleSubcategoryToggle = (subcategory) => {
    setFilters((prevFilters) => ({
      ...prevFilters,
      selectedSubcategories: prevFilters.selectedSubcategories.includes(subcategory)
        ? prevFilters.selectedSubcategories.filter((item) => item !== subcategory)
        : [...prevFilters.selectedSubcategories, subcategory],
    }));
  };

  const handleSortChange = (value) => {
    setFilters((prevFilters) => ({
      ...prevFilters,
      sortOption: value,
    }));
  };

  const handlePriceChange = (e) => {
    const { name, value } = e.target;
    setFilters((prevFilters) => ({
      ...prevFilters,
      [name]: value,
    }));
  };

  const applyFilters = () => {
    dispatch(updateFilter(filters));
    setIsModalOpen(false); 
  };

  const resetFilters = () => {
    setFilters({
      selectedCategory: "",
      selectedSubcategories: [],
      sortOption: "",
      minPrice: "",
      maxPrice: "",
    });
    dispatch(resetFilter());
  };

  return (
    <div className="relative">
      <Dialog open={isModalOpen} onOpenChange={setIsModalOpen}>
        <DialogTrigger asChild>
          <Button className="bg-orange-600 hover:bg-orange-700 w-full sm:w-auto">
            <svg
              xmlns="http://www.w3.org/2000/svg"
              className="w-5 h-5 mr-2"
              fill="none"
              viewBox="0 0 24 24"
              stroke="currentColor"
              strokeWidth={2}
            >
              <path
                strokeLinecap="round"
                strokeLinejoin="round"
                d="M3 4a1 1 0 011-1h16a1 1 0 011 1v2a1 1 0 01-.293.707L13 12.414V20a1 1 0 01-.553.894l-4 2A1 1 0 017 22V12.414L3.293 6.707A1 1 0 013 6V4z"
              />
            </svg>
            <span>Filter</span>
          </Button>
        </DialogTrigger>
        <DialogContent className="sm:max-w-md">
          <DialogHeader>
            <DialogTitle>Filters</DialogTitle>
          </DialogHeader>
          <div className="space-y-6">
            <div>
              <h3 className="text-lg font-medium mb-2">Categories</h3>
              <RadioGroup
                value={filters.selectedCategory}
                onValueChange={handleCategoryChange}
                className="space-y-2"
              >
                {data?.map((category) => (
                  <div key={category.id} className="flex items-center space-x-2">
                    <RadioGroupItem value={category.name} id={category.id} />
                    <Label htmlFor={category.id}>{category.name}</Label>
                  </div>
                ))}
              </RadioGroup>
            </div>

            {filters.selectedCategory && (
              <div>
                <h3 className="text-lg font-medium mb-2">Subcategories</h3>
                <div className="space-y-2">
                  {data
                    .find((cat) => cat.name === filters.selectedCategory)
                    ?.subCategories?.map((subcategory) => (
                      <div key={subcategory.id} className="flex items-center space-x-2">
                        <Checkbox
                          id={subcategory.id}
                          checked={filters.selectedSubcategories.includes(subcategory.name)}
                          onCheckedChange={() => handleSubcategoryToggle(subcategory.name)}
                        />
                        <Label htmlFor={subcategory.id}>{subcategory.name}</Label>
                      </div>
                    ))}
                </div>
              </div>
            )}

            <div className="flex space-x-4">
              <div className="w-1/2">
                <Label htmlFor="minPrice">Min Price</Label>
                <Input
                  type="number"
                  id="minPrice"
                  name="minPrice"
                  value={filters.minPrice}
                  onChange={handlePriceChange}
                  placeholder="Min"
                />
              </div>
              <div className="w-1/2">
                <Label htmlFor="maxPrice">Max Price</Label>
                <Input
                  type="number"
                  id="maxPrice"
                  name="maxPrice"
                  value={filters.maxPrice}
                  onChange={handlePriceChange}
                  placeholder="Max"
                />
              </div>
            </div>

            <div>
              <h3 className="text-lg font-medium mb-2">Sort By</h3>
              <Select value={filters.sortOption} onValueChange={handleSortChange}>
                <SelectTrigger className="w-full">
                  <SelectValue placeholder="Select Sort Option" />
                </SelectTrigger>
                <SelectContent>
                  <SelectItem value="low to high">Price Low to High</SelectItem>
                  <SelectItem value="high to low">Price High to Low</SelectItem>
                </SelectContent>
              </Select>
            </div>

            <div className="flex justify-between">
              <Button onClick={applyFilters} className="bg-orange-500 hover:bg-orange-600">
                Apply Filters
              </Button>
              <Button onClick={resetFilters} className="bg-orange-900 hover:bg-orange-600">
                Reset
              </Button>
            </div>
          </div>
        </DialogContent>
      </Dialog>
    </div>
  );
}

export default Filter;